import React, { Component } from 'react';
import './Head.css';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import {BottomNavigation, BottomNavigationItem} from 'material-ui/BottomNavigation';
import ReactDOM from 'react-dom';
import axios from 'axios';

import Home from 'material-ui/svg-icons/content/flag';
import Map from 'material-ui/svg-icons/maps/map';
import Graduation from 'material-ui/svg-icons/social/school';
import Check from 'material-ui/svg-icons/av/featured-play-list';

import HomeItem from './Home/Home.js';
import MapItem from './Map/Map.js';
import GraduationItem from './Graduation/Graduation.js';
import CreditItem from './Credit/Credit.js';

import FadeIn from 'react-fade-in';


const homeIcon = <Home />;
const mapIcon = <Map />;
const graduationIcon = <Graduation />;
const checkIcon = <Check />;

axios.get('/students/info').then(res => {
    res.status // HTTP response code (e.g., 200, 401)
    res.data // object parsed from HTTP response body
    res.headers // HTTP presonse headers
    console.log(res.data);

}).catch(err => {
    console.log(err);
});


var data = [{
    name : "",
    grad : "",

},
];

class Head extends Component {

        state = {
            selectedIndex: 0,
            styleButton: {
                fontFamily: 'Noto Sans CJK TC',
                background: '#EEEEEE',
            },
            styleindex: {
                display : 'inline',
            },
        };

  	select(index){
  		this.setState({styleindex:
			{display : 'none',
			}
        });
  		if(index===0){
        	ReactDOM.render(
				<FadeIn>
					<HomeItem />
				</FadeIn>,
            document.getElementById('page'));
		}
        else if(index===1){
            ReactDOM.render(
				<div>
					<FadeIn>
						<MapItem
							data = {data}/>
					</FadeIn>
				</div>,
				document.getElementById('page'));
        }
        else if(index===2){
            ReactDOM.render(
            	<p>
					<FadeIn>
						<GraduationItem />
					</FadeIn>
				</p>,
                document.getElementById('page'));
        }
        else if(index===3){
            ReactDOM.render(
				<a>
				<FadeIn>
					<CreditItem />
				</FadeIn>
                </a>,
                document.getElementById('page'));
        }

  		this.setState({selectedIndex: index});
  	}
    
	render() {
	    return (
		    <div id="Head">
				<div id="ontopDiv">
					<div className="Head-header" >
						<div id="rectangle1"></div>
						<div id="h1"></div>
						<div id="h2">NCTU Curriculum Assistant</div>
					</div>
					<div id="adjust">
					<MuiThemeProvider zDepth={1}>
						<BottomNavigation selectedIndex={this.state.selectedIndex}>
						  <BottomNavigationItem
							label=""
							icon={homeIcon}
							style={this.state.styleButton}
							onTouchTap={() => this.select(0)}
						  />
						  <BottomNavigationItem
							label=""
							icon={mapIcon}
							style={this.state.styleButton}
							onTouchTap={() => this.select(1)}
						  />
						  <BottomNavigationItem
							label=""
							icon={graduationIcon}
							style={this.state.styleButton}
							onTouchTap={() => this.select(2)}
						  />
						  <BottomNavigationItem
							label=""
							icon={checkIcon}
							style={this.state.styleButton}
							onTouchTap={() => this.select(3)}
						  />
						</BottomNavigation>
					</MuiThemeProvider>
					</div>
				</div>
				<div id="topRec">
				</div>

				<div id="page"></div>
				<div style={this.state.styleindex}>
					<FadeIn>
						<HomeItem/>
					</FadeIn>
				</div>
				<footer>Copyright @2017 NCTUCS </footer>
	  		</div>
	    );
	  }
}

export default Head;



